import React, { Component } from 'react'
import { Div } from 'glamorous'
import TimeLine from '../TimeLine'
import Events from '../Events'

export default class TimeTable extends Component {
  state = {
    clicked: false
  }

  innerStyles = {
    height: '100%',
    paddingBottom: '17px',
    boxSizing: 'content-box',
    overflowX: 'scroll',
    overflowY: 'hidden',
    backgroundColor: '#f6f7f9'
  }

  outerStyles = {
    overflow: 'hidden',
    userSelect: 'none',
    cursor: 'grab',
    '@media(max-width: 540px)': {
      width: '50%'
    }
  }

  componentDidMount() {
    this.move()
  }

  shouldUseDragging = () => {
    const isIE = !!window.MSInputMethodContext && !!document.documentMode
    const isSafari = navigator.userAgent.indexOf('Safari') > -1 && 
          navigator.userAgent.indexOf('Chrome') === -1

    return !isIE && !isSafari
  }

  triggerClicked = () => {
    this.setState(prevState => ({
      clicked: !prevState.clicked
    }))

    this.timeTable.style.cursor = this.state.clicked ? 'grabbing' : 'grab'
  }

  handleDrag = e => {
    e.preventDefault()

    if (this.state.clicked) {
      this.timeTable.scrollLeft = e.movementX < 0 
      ?
        this.timeTable.scrollLeft + 20 
      :
        this.timeTable.scrollLeft - 20
    }
  }

  handleScroll = e => {
    e.preventDefault()

    if(e.deltaY > 0) {
      this.timeTable.scrollLeft = this.timeTable.scrollLeft + 20
    }

    else {
      this.timeTable.scrollLeft = this.timeTable.scrollLeft - 20
    }
  }

  move = () => {
    if (this.shouldUseDragging()) {
      this.timeTable.addEventListener('mousedown', this.triggerClicked)
      this.timeTable.addEventListener('mouseup', this.triggerClicked)
      this.timeTable.addEventListener('mousemove', this.handleDrag)
    }

    else {
      this.timeTable.addEventListener('wheel', this.handleScroll)
    }
  }
  
  render() {
    return (
      <Div css={this.outerStyles}>
        <Div 
          css={this.innerStyles}
          innerRef={ref => this.timeTable = ref}
          /*onMouseMove={e => this.shouldUseDragging ? this.handleDrag(e) : null}
          onMouseDown={e => this.shouldUseDragging ? this.triggerClicked(e) : null}
          onMouseUp={e => this.shouldUseDragging ? this.triggerClicked(e) : null}*/
        >
          <TimeLine />
          <Events />
        </Div>
      </Div>
    )
  }
}


